import React from "react";
import CardContent from "../../components/CardComponent";
import { Grid, Paper, Box } from "@mui/material";

import "../components/styles/Activities.css";

export default function ActivityView() {
  /*   */
  const actList = [
    {
      title: "  SW  (SSAFY) 6",
      contents:
        "  SW        ",
      date: "2021.07.07~ ",
    },

    {
      title: "SSAFY 6 ",
      contents: "1  3  & 2 // ",
      date: "2021.07.07~ ",
    },
    {
      title: "SSAFY Alumni 1",
      contents: "  SW     ()",
      date: "2022.03~",
    },
    {
      title: "Residence Assistance",
      contents:
        "           ",
      date: "2018.01.01 ~ 2020.05.14 ",
    },
  ];
  const pjtList = [
    {
      title: "SSAFY 6    ",
      contents: "      ",
      date: "2021.11.18 - 2021.11.26 (3 )",
    },
    {
      title: "SSAFY 6    ",
      contents: "    SNS ",
      date: "2022.01.10 - 2022.02.18 (6 )",
    },
    {
      title: "SSAFY 6     ",
      contents: "      ",
      date: "2022.02.28 - 2022.04.08 (6 )",
    },
    {
      title: "Lane Department Tutor",
      contents:
        "WVU  CS/CPE/EE ,            ",
      date: "2019.09.02 ~ 2020.05.14 ",
    },
  ];

  return (
    <>
      <Box sx={{ display: "flex", flexWrap: "wrap", mt: -15 }}>
        <Paper
          elevation={16}
          sx={{ m: 1, width: "70vw", height: "75vh", borderRadius: "25px" }}
        >
          <Grid container className="activities-whiteBox">
            {/* lights */}
            <Grid className="circle-red" />
            <Grid className="circle-yellow" />
            <Grid className="circle-green" />
          </Grid>

          <Grid container direction="column" rowSpacing={-2}>
            <Grid container direction="row">
              <Grid item xs={6}>
                {actList.map((act) => (
                  <CardContent key={act.title} activity={act} />
                ))}
              </Grid>
              <Grid item xs={6}>
                {pjtList.map((act) => (
                  <CardContent key={act.title} activity={act} />
                ))}
              </Grid>
            </Grid>
          </Grid>
        </Paper>
      </Box>
    </>
  );
}
