import React from "react";
import LargeTypedModule from "../../../components/LargeTypedModule";
import LargeColorTypedModule from "../../../components/LargeColorTypedModule";
export default function InterestText() {
  return (
    <>
      <LargeTypedModule
        isMobile={false}
        msg={"..."}
        time={0}
        inLine={true}
        lastWord={false}
      />
      <LargeColorTypedModule
        isMobile={false}
        msg={""}
        time={600}
        inLine={true}
        lastWord={false}
      />
      <LargeTypedModule
        isMobile={false}
        msg={""}
        time={1100}
        inLine={true}
        lastWord={false}
      />
      <LargeColorTypedModule
        isMobile={false}
        inLine={true}
        msg={"  "}
        time={1250}
        lastWord={false}
      />
    </>
  );
}
